.navBar {
    display: flex;
    background-color: white;
    flex-direction: row;
    justify-content: space-evenly;
    border-top: 1px solid rgb(100, 100, 100);
    width: 100%;
    position: fixed;
    bottom: 0;
    font-size: 0.8em;
}

.navBar .iconBtn {
    margin: 0;
    padding: 0px;
    padding-top: 3px;
}

.navBar p {
    margin-top: -7px;
}

.activeNavIcon, .activeNavIcon .navIcon {
    color: #A708A7;
}

@media only screen and (max-width: 992px) {
   .navBar .iconBtn:hover, .navBar .iconBtn:focus {
       background-color: transparent;
   }
}
